/* Add any App-specific styles here */
.container {
    /* Container styles are now in index.css, but you can override or add more here */
}

.bento-grid {
    /* Bento grid layout will be primarily handled here or in a dedicated layout component if it gets complex */
    /* Example: override or specify grid columns for the main app layout */
    /* grid-template-columns: repeat(2, 1fr); for a two-column layout */
     grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); /* Responsive columns */
}

header {
    text-align: center;
    margin-bottom: 30px;
    padding: 20px 0;
    border-bottom: 1px solid #eee;
}

header h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    margin-bottom: 5px;
}

header h1 i {
    color: #3498db; /* Icon color for header */
    margin-right: 10px;
    font-size: 0.9em; /* Adjust icon size relative to h1 */
}

header p {
    font-size: 1.1em;
    color: #555;
}


footer {
    text-align: center;
    margin-top: 40px;
    padding: 20px 0;
    border-top: 1px solid #eee;
    font-size: 0.9em;
    color: #777;
}

/* Specific styles for bento boxes if not covered globally */
.code-input-box .file-drop-area {
    border: 2px dashed #dce4f0;
    padding: 30px;
    text-align: center;
    border-radius: 8px;
    background-color: #f8f9fa;
    transition: background-color 0.2s ease;
}

.code-input-box .file-drop-area.dragover {
    background-color: #e9ecef;
    border-color: #b0c4de;
}

.code-input-box .upload-icon {
    font-size: 3em; /* Increased icon size */
    color: #3498db;
    margin-bottom: 15px;
}

.code-input-box .file-drop-area p {
    margin: 5px 0;
    color: #555;
}

.code-input-box .or-text {
    font-weight: 500;
    color: #777;
    margin: 10px 0;
}

.code-input-box .custom-file-upload {
    display: inline-flex; /* Changed from inline-block for better alignment with icon */
    align-items: center;
    padding: 10px 18px;
    background-color: #3498db;
    color: white;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 500;
    transition: background-color 0.2s ease;
}

.code-input-box .custom-file-upload:hover {
    background-color: #2980b9;
}

.code-input-box .custom-file-upload i {
    margin-right: 8px;
}

.code-input-box .file-list-preview {
    margin-top: 20px;
}

.code-input-box .file-list-preview h4 {
    margin-bottom: 10px;
    font-weight: 500;
}

.code-input-box .file-list-preview ul {
    list-style: none;
    padding: 0;
}

.code-input-box .file-list-preview li {
    background-color: #f9f9f9;
    padding: 8px 12px;
    border-radius: 4px;
    margin-bottom: 5px;
    font-size: 0.9em;
    color: #333;
    border: 1px solid #eee;
}

.url-input-box .url-input-group {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
}

.url-input-box .url-input-group input {
    flex-grow: 1;
    margin-bottom: 0; /* Remove default margin from global input style */
}

.url-input-box .loading-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; /* Stack spinner and text vertically */
    color: #555;
    font-size: 0.95em;
}

.url-input-box .loading-indicator .spinner {
    width: 24px;
    height: 24px;
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid #3498db; /* Blue */
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 10px; /* Space between spinner and text */
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.context-output-box .context-controls {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
}

.context-output-box .context-controls button {
    flex-grow: 1; /* Make buttons take equal width if needed, or adjust as per design */
}

.settings-box .options-content .coming-soon {
    color: #777;
    font-style: italic;
    text-align: center;
    padding: 20px;
    background-color: #f8f9fa;
    border-radius: 6px;
} 